import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";
import "./staticTable.css";

const StaticTable = ({
  columnHeaders,
  handleCheckboxChange,
  checkedItems,
  handleSelectChange,
  selectedAttributes,
  mappedAttributes, // Passed mapped attributes
  mandatoryAttributes,
}) => {
  console.log("Mapped Attributes prop:", mappedAttributes);
  console.log("columnHeaders", columnHeaders);
  console.log("static table recieved manditory Attribute", mandatoryAttributes);

  console.log(
    "Mapped Attributes with NLS:",
    mappedAttributes.map((attr) => ({
      nls: attr.uiLabel,
      name: attr.backendName,
    }))
  );
  console.log("Column Headers to check:", columnHeaders);

  // Helper function to check if header matches any nls value
  const hasMatchingNLS = (header) => {
    const hasMatch = mappedAttributes.some(
      (attr) => attr.uiLabel.toLowerCase() === header.toLowerCase()
    );

    console.log(
      `Checking header "${header}":`,
      hasMatch ? "‚úÖ NLS match found" : "‚ùå No NLS match"
    );
    return hasMatch;
  };

  return (
    <Table bordered className="table-light ">
      <thead>
        <tr>
          <th>Column Name</th>
          <th>Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {columnHeaders.map((header, index) => {
          const isMandatory = mandatoryAttributes.includes(header);
          const hasNLSMatch = hasMatchingNLS(header);
          return (
            <tr
              key={index}
              className={!hasNLSMatch || isMandatory ? "disabled-row" : ""}
            >
              <td>{header}</td>
              <td>
                {hasNLSMatch ? (
                  <CustomSelect
                    index={index}
                    selectedValue={selectedAttributes[index] || header}
                    onChange={handleSelectChange}
                    size="lg"
                    className="w-100"
                    disabled={isMandatory}
                    options={{
                      defaultLabel: header,
                      list: mappedAttributes.map((attr) => ({
                        value: attr.backendName,
                        label: attr.uiLabel,
                      })),
                    }}
                  />
                ) : (
                  <span className="text-muted">
                    No matching attribute found in system
                  </span>
                )}
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
};

export default StaticTable;
